import Vue from 'vue'
import App from './App.vue'
import router from './router'
import YmapPlugin from 'vue-yandex-maps'

const settings = {
  apiKey: 'bc4904a2-7dc1-4ac5-92b7-8de15e75a5b2',
  lang: 'ru_RU',
  coordorder: 'latlong'
}
Vue.use(YmapPlugin, settings)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
