<template>
  <div class="fb-col-home">
    <div class="bg">
      <div style="display: block; height: 70px;"></div>
      <h2 class="title">Данил  <div class="rings"></div> Ирина</h2>
      <h3 class="title">Мы хотим пригласить вас на это торжество 01.08.2020. Начало банкета: 17:00.</h3>
      <svg class="DbArrow" viewBox="0 0 24 24" @click="Show">
        <use class="a" xlink:href="../assets/arrow-down.svg#arow1"></use>
        <use class="b" xlink:href="../assets/arrow-down.svg#arow2"></use>
      </svg>
    </div>
    <h1 class="title">Загородный комплекс Петушки. (с. Высокое, 125).</h1>
    <YandexMap v-if="ShowMap"/>
  </div>
</template>
<script>
import YandexMap from '../components/YandexMap.vue'
export default {
  name: 'home',
  data: () => ({
    ShowMap: false
  }),
  components: {
    YandexMap
  },
  methods: {
    Show () {
      this.ShowMap = true
      this.WaitingElement()
    },
    WaitingElement () {
      var ms = 1000
      var timerId = setTimeout(function tick () {
        var el = document.querySelector('#myYndexMap')
        if (el !== null) {
          window.location.href = '#myYndexMap'
          clearTimeout(timerId)
        } else {
          timerId = setTimeout(tick, ms)
        }
      }, ms)
    }
  }
}
</script>

