import axios from 'axios'
export default {
  data () {
    return {
      content: {},
      exceptionArr: ['Surname', 'Name', 'Жених', 'Невеста']
    }
  },
  created () {
    this.getData()
  },
  methods: {
    getData () {
      axios('https://spreadsheets.google.com/feeds/list/1tFcDbWFRTYtHbs4opQx-qz18hxZS6myCFko6RUnZrPk/od6/public/values?alt=json', {
        method: 'GET'
      }).then((response) => {
        this.content = this.getAndToObj(response)
      })
      console.log('GET axios')
    },
    getAndToObj (response) {
      var Obj = response.data.feed.entry
      var ObjNameStrok = this.parsToNameStrok(response)

      var row = 0
      var name = ''

      var GObj = {}
      var mas = []
      for (var key in ObjNameStrok) {
        mas = []
        for (var st in ObjNameStrok[key]) {
          row = key
          name = ObjNameStrok[key][st]
          mas.push(Obj[row][name].$t)
        }
        GObj[key] = mas
      }
      return GObj
    },
    parsToNameStrok (data) {
      var arrData = data.data.feed.entry
      var myRe = new RegExp('gsx\\$', 'g')
      var masGsx = []
      var ItogObg = {}
      var temp = []
      for (var i = 0; i < arrData.length; i++) {
        temp = Object.keys(arrData[i])
        masGsx = []
        for (var j = 0; j < temp.length; j++) {
          if (myRe.test(temp[j])) {
            myRe.lastIndex = 0
            masGsx.push(temp[j])
          }
        }
        ItogObg[i] = masGsx
      }
      return ItogObg
    },
    filter (data, numCol, exceptionArr) {
      var numColDefault = (numCol === undefined)
      var boolDelete = false
      var filterData = {}
      var tempmas = []
      for (var row in data) {
        tempmas = []
        numCol = numColDefault ? data[row].length : numCol
        for (var i = 0; i < numCol; i++) {
          if (this.myRegExp(exceptionArr, data[row][i] || '')) {
            tempmas.push(data[row][i] || '')
          } else boolDelete = true
        }
        filterData[row] = tempmas
        if (boolDelete) {
          delete filterData[row]
          boolDelete = false
        }
      }
      if (exceptionArr !== undefined) {
        var count = 0
        for (var g in filterData) {
          delete Object.assign(filterData, { [count]: filterData[g] })[g]
          count++
        }
      }
      return filterData
    },
    myRegExp (mas, string) {
      if (mas === undefined) {
        return true
      }
      var str = '('
      for (var i in mas) {
        if (parseInt(i) + 1 !== mas.length) {
          str += mas[i] + '|'
        } else str += mas[i]
      }
      str += ')'
      // console.log(str)
      var myRe = new RegExp(str, 'g')
      if (myRe.test(string)) return false
      else return true
    },
    selectId (id) {
      var data = this.filter(this.content, 2, this.exceptionArr)
      return data[id]
    }
  }
}
